import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';

import './style.css';

export default class ContactForm extends PureComponent {
  constructor(props) {
    super(props);

    this.formRef = React.createRef();
    this.handleSubmit = this.handleSubmit.bind(this);
    this.fetchSentEmail = this.fetchSentEmail.bind(this);
    this.clearStatus = this.clearStatus.bind(this);

    //none, progress, failed, success
    this.state = {status: 'none'};
  }

  handleSubmit(evt) {
    evt.preventDefault();
    const inputs = this.formRef.current.querySelectorAll('input');
    const textArea = this.formRef.current.querySelectorAll('input');

    const data = {
      name: inputs[0].value,
      email: inputs[1].value,
      subject: inputs[2].value || '',
      message: textArea[0].value,
    };

    this.fetchSentEmail(data);
  }

  fetchSentEmail(data) {
    this.setState({status: 'progress'});

    fetch('../sendEmail.php', {
      method: 'POST',
      body: JSON.stringify(data),
      headers: new Headers({'Content-Type': 'application/json'})
    }).then(res => res.json())
        .catch(() => {
          this.setState({status: 'failed'});
          this.clearStatus();
        })
        .then(response => {
          const status = response === 200 ? 'success' : 'failed';
          this.setState({status: status});
          this.clearStatus();
        });
  }

  clearStatus() {
    setTimeout(() => {this.setState({status: 'none'})}, 5000)
  }

  render() {
    return (
        <React.Fragment>
          <form className="" ref={this.formRef} onSubmit={this.handleSubmit}>
            <input type="text" required autoComplete="name"
                   className="form-control form-group" name="name" placeholder={this.props.namePlaceholder}/>
            <input type="email" required autoComplete="email"
                   className="form-control form-group" name="email" placeholder={this.props.emailPlaceholder}/>
            <input type="text" autoComplete="off"
                   className="form-control form-group" name="subject" placeholder={this.props.subjectPlaceholder}/>
            <div className="row">
              <div className="col-12 col-sm-9">
              <textarea rows="3" required autoComplete="off" className="form-control mb-3 mb-sm-0" name="message"
                        placeholder={this.props.messagePlaceholder}/>
              </div>
              <div className="col-12 col-sm-3 button-container">
                <button type="submit" className="button dark">{this.props.buttonText}</button>
              </div>
            </div>
          </form>
          <p className={`feedback-message status-${this.state.status}`}>
            {this.state.status === 'progress' && <span>{this.props.sendLoadingMessage}</span>}
            {this.state.status === 'success' && <span>{this.props.sendSuccessMessage}</span>}
            {this.state.status === 'failed' && <span>{this.props.sendErrorMessage}</span>}
          </p>
        </React.Fragment>
    );
  }
}

ContactForm.propTypes = {
  namePlaceholder: PropTypes.string.isRequired,
  emailPlaceholder: PropTypes.string.isRequired,
  subjectPlaceholder: PropTypes.string.isRequired,
  messagePlaceholder: PropTypes.string.isRequired,
  buttonText: PropTypes.string.isRequired,
  sendSuccessMessage: PropTypes.string.isRequired,
  sendErrorMessage: PropTypes.string.isRequired,
  sendLoadingMessage: PropTypes.string.isRequired,
};




