import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import NavLink from "./NavLink";
import forIn from 'lodash/forIn';

export default class NavItem extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {expanded: !this.props.languageMenu};

        this.toggleExpanded = this.toggleExpanded.bind(this);
    }

    toggleExpanded() {
        this.setState(prevState => ({expanded: !prevState.expanded}));
    }

    renderSubItems(subItems) {
        const items = [];

        forIn(subItems, (value, key) => {
            const url = this.props.languageMenu
                ? value.route
                : this.props.baseUrl + this.props.route;

            items.push(
                <NavLink
                    {...value}
                    key={key}
                    route={url}
                    closeMenu={this.props.closeMenu}/>
            );
        });

        return items;
    }

    render() {
        const expanded = this.state.expanded ? ' open ' : '';

        return (
            <NavLink toggleExpanded={this.toggleExpanded}
                     closeMenu={this.props.closeMenu}
                     displayName={this.props.displayName}
                     target={this.props.target}
                     route={this.props.baseUrl + this.props.route}
                     className={this.props.className}>
                {this.props.subRoutes &&
                <ul className={`dropMenu ${expanded}`}>{this.renderSubItems(this.props.subRoutes)}</ul>
                }
            </NavLink>

        );
    }
}

NavItem.propTypes = {
    baseUrl: PropTypes.string,
    closeMenu: PropTypes.func.isRequired,
    languageMenu: PropTypes.bool,
    className: PropTypes.string,

    displayName: PropTypes.string,
    target: PropTypes.string,
    route: PropTypes.string,

    subRoutes: PropTypes.shape({
        displayName: PropTypes.string,
        target: PropTypes.string,
        route: PropTypes.string,
    })
};



