import React, {PureComponent} from 'react';
import PropTypes from "prop-types";
import Links from "./Links/Links";
import Contact from "./Contact/Contact";
import Logo from "../../../assets/icons/Logo";
import ReactHtmlParser from "react-html-parser";
import './Footer.css';

export default class Footer extends PureComponent {
  render() {

    return (
        <footer className="mastFooter">
          <div className="inner">

            <div className="container">
              <div className="row">

                <div className="col-lg-5 col-12 mb-4 footer--navigation">
                  <Links {...this.props.navItems}
                         footerTitle={this.props.footer.navigationTitle}
                         baseUrl={this.props.baseUrl}
                  />
                </div>

                <div className="col-lg-4 col-12 mb-4 footer--contact">
                  <Contact {...this.props.contactInfo} footerTitle={this.props.footer.contactTitle}/>
                </div>

                <div className="col-lg-3 col-12 text-center mt-4 mt-lg-2 footer--rights">
                  <div className="logo">
                    <a href={this.props.baseUrl + '/main'}><Logo/></a>
                  </div>
                  <p className="copyright">
                    <span>{ReactHtmlParser(`&copy; ${this.props.footer.rights.name}`)}</span>
                    <span> {this.props.footer.rights.text}</span>
                  </p>
                  <p className="developer">
                    <a href={this.props.footer.rights.developerLink}>{this.props.footer.rights.developText}</a>
                  </p>
                </div>

              </div>
            </div>
          </div>
        </footer>
    );
  }
}

Footer.propTypes = {
  baseUrl: PropTypes.string.isRequired,
  navItems: PropTypes.object.isRequired,
  languages: PropTypes.array.isRequired,
  logo: PropTypes.shape({alt: PropTypes.string}),
  contactInfo: PropTypes.object.isRequired,
  footer: PropTypes.shape({
    navigationTitle: PropTypes.string.isRequired,
    contactTitle: PropTypes.string.isRequired,
    rights: PropTypes.shape({
      name: PropTypes.string,
      text: PropTypes.string,
      developerText: PropTypes.string,
      developerLink: PropTypes.string
    }).isRequired,
  }).isRequired
};


