import React, {PureComponent} from 'react';
import './Navbar.css';

import forIn from 'lodash/forIn';
import NavItem from './NavItem';
import PropTypes from 'prop-types';

export default class Navbar extends PureComponent {
  render() {
    const menuOpen = this.props.menuExpanded ? 'open' : '';
    const navItems = [];
    const {language, ...others} = this.props.navItems;

    forIn(others, (value, key) => {
      navItems.push((
          <NavItem {...value}
                   key={key}
                   baseUrl={this.props.baseUrl}
                   closeMenu={this.props.toggleMenuExpanded}
          />
      ));
    });

    return (
        <nav id="MenuBar">
          <ul className={`navList clearfix ${menuOpen}`}>
            {navItems}

            {/*TODO add languages after Mihaela translates the text*/}
            {/*Languages nav menu*/}
            {/*<NavItem*/}
            {/*className={'language-menu'}*/}
            {/*languageMenu={true}*/}
            {/*displayName={this.props.navItems.language.displayName}*/}
            {/*closeMenu={this.props.toggleMenuExpanded}*/}
            {/*subRoutes={this.props.navItems.language.subRoutes}*/}
            {/*/>*/}
          </ul>
        </nav>
    );
  }
}

Navbar.propTypes = {
  menuExpanded: PropTypes.bool,
  toggleMenuExpanded: PropTypes.func.isRequired,
  baseUrl: PropTypes.string,

  navItems: PropTypes.shape({
    services: PropTypes.object,
    collaborators: PropTypes.object,
    referrals: PropTypes.object,
    aboutMe: PropTypes.object,
    contact: PropTypes.object,
    language: PropTypes.object
  }),
};


